
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'

import sha512 from 'js-sha512' //sha512加密
export default {
	data() {
		return {
			tel: '',
			vCode: '',
			password: '',
			vCodeStart: false,//验证码按钮开关
			time: 60,//验证码倒计时
			telNoRepeat: false //异步校验手机号码是否重复
		}
	},
	computed: {},
	methods: {
		//验证号码是否被注册
		checkLoginName() {
			this.telNoRepeat = true;
			this.$validator.validateAll({
				tel: this.tel
			}).then(() => {
				this.$common.ajax({
					data: {
						header: {
							action: 'CheckMobile',
						},
						body: {
							mobile: this.tel,
						}
					},
					success: (res) => {
						this.$vux.toast.show({
							text: '该号码未注册',
							type: 'text'
						});
					},
					error: () => {
						this.telNoRepeat = false;
					}
				})
			}).catch(() => {
				this.$vux.toast.show({
					text: this.$validator.errorBag.first('tel'),
					type: 'text'
				});
			});
		},
		//获取验证码
		getPhoneValidateCode() {
			this.$validator.validateAll({
				tel: this.tel
			}).then(() => {
				if(!this.telNoRepeat) {
					this.vCodeStart = true;
					this.$common.ajax({
						data: {
							header: {
								action: 'SmsValidateCode',
							},
							body: {
								mobile: this.tel,
							}
						},
						success: (res) => {
							this.$vux.toast.show({
								text: '验证码已发送至' + this.tel + '，请注意查收',
								width: '50%',
								type: 'text'
							})
						}
					})
				} else {
					this.$vux.toast.show({
						text: '该号码未注册',
						type: 'text'
					});
				}
			}).catch(() => {
				this.$vux.toast.show({
					text: this.$validator.errorBag.first('tel'),
					type: 'text'
				});
			});
		},
		//验证码倒计时结束
		finish() {
			this.vCodeStart = false;
			this.time = 60;
		},
		//提交表单
		submitData() {
			this.$validator.validateAll().then(() => {
				if(!this.telNoRepeat) {
					this.$common.ajax({
						data: {
							header: {
								action: 'EditPasswordByValidateCode',
							},
							body: {
								mobile: this.tel,
								validatecode: this.vCode,
								newpassword: sha512(this.password)
							}
						},
						success: (res) => {
							this.$vux.toast.show({
								text: res.cause,
								onHide: () => {
									this.$router.push({
										path: 'login'
									});
								}
							})
						}
					})
				} else {
					this.$vux.toast.show({
						text: '该号码未注册',
						type: 'text'
					});
				}

			}).catch(() => {
				this.$vux.toast.show({
					text: this.$validator.errorBag.all()[0],
					type: 'text'
				});
			});
		}
	},
	mounted() {
		document.querySelector('.putRecourse').style.display = 'none';
	},
	components: {
		XInput,
		Group,
		XButton,
		XHeader,
		Countdown
	}
}



