
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import memberFamily from 'components/memberFamily.vue'
import mixins from 'assets/js/mixins.js'
import echartOption from 'assets/js/echartOption.js'
export default {
	name: 'healthData',
	mixins: [mixins.dateTime],
	data() {
		return {
			userName:'家庭成员',//用户名称
			userId: '', //用户id
			data: {}, //数据
			tabIndex: 0, //tab选项，默认0
			echartOption: {},
			memberFamilyShow:false,
		}
	},
	computed: {	
		hasData(){
			return this.data.length>0&&this.data!="null"?true:false
		}
	},
	watch:{
		startTime(){				
			this.getData({type:Object.keys(this.$common.healthDataType)[this.tabIndex]});
		}
	},
	methods: {
		//获取数据
		getData(opt) {
			this.$refs.echart.showLoading();
			this.$common.ajax({
				loading:false,
				result: true,
				data: {
					header: {
						action: 'GetHealthData',
					},
					body: {
						userid: this.userId,
						datatype: opt.type,
						starttime: this.startTime,
						endtime: ''
					}
				},
				success: (res) => {						
					this.$refs.echart.hideLoading();
					this.data = res;
					if(this.data&&this.data.length>0){
						this.initEchart(opt)
					}
					
					
				}
			})
		},
		//实例化图表
		initEchart(opt) {				
			this.echartOption = echartOption[opt.type](this.data);
		},
		memberFamilyChange(opt){
			this.userName=opt.RealName;
			this.userId=opt.ID;
			this.tabIndex=0;
			this.getData({type:101});
		}			
	},
	mounted() {	
		this.userId=this.$common.getLocalStorage('user').doctorid;
		this.getData({type:101});
	},

	components: {
		Tab,
		TabItem,
		XHeader,
		memberFamily
	}
}



